.header {
    min-height: 53px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.header ul {
    display: flex;
    justify-content: center;
    align-items: center;
}
.header li {
    cursor: pointer;
    margin: 0 10px;
}
.header li a:hover {
    color: #ff6969;
}
/* BURGER */
.fa {
    color: #ff6969;
}
.topnav {
    overflow: hidden;
    background-color: #333;
    width: 100%;
}
.topnav a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}
.topnav a:hover {
    color: #ff6969;;
}
.topnav a.active {
    color: #ff6969;
}
.topnav .icon {
    display: none;
}
@media screen and (max-width: 600px) {
    .topnav a:not(:first-child) {display: none;}
    .topnav a.icon {
        float: right;
        display: block;
    }
}
@media screen and (max-width: 600px) {
    .topnav.responsive {position: relative;}
    .topnav.responsive .icon {
        position: absolute;
        right: 0;
        top: 0;
}
    .topnav.responsive a {
        float: none;
        display: block;
        text-align: left;
    }
}
/* END BURGER */
/* TABS */
a{
    color: #666666;
}
.nav-tabs{
    display: flex;
}
.nav-tab{
    margin-right: 10px;
    border-bottom: 0;
    text-decoration: none;
    font-size: 18px;
    font-weight: 700;
    border: 1px solid #666666;
    padding: 4px;
}
.nav-tab.active{
    color: #ff6969;
    border: 1px solid #ff6969;
    cursor: default;               
}
.nav-tab:hover {
    color: #ff6969;
    border: 1px solid #ff6969;
}
.tab-content{
    background: aliceblue;
    
}
.border_content {
    border-left: 1px solid black;
    border-right: 1px solid black;
}
.tab-pane{
    display: none;
    padding: 5px;
    margin-top: 10px;
    border: 1px solid #ff6969;
}
.tab-pane.active{
    display: flex;
}
/* CONTENT */
.first_span {
    color: $black;
}
.second_span {
    color: $orange;
}
/* END CONTENT */
.youtube {
    width: 100%;
    min-height: 350px;
    height: 100%;
}
.footer {
    height: 50px;
    .container {
        display: flex;
        align-items: center;
        background-color: rgb(43, 43, 43);
        height: 100%;
    }
    a {
        text-decoration: none;
        color: $orange;
    }
    a:hover {
        color: rgb(255, 255, 255);
    }
}